  text-decoration: none;
}
main.nav-frame nav.main-nav ul.main-nav__list li.main-nav__item a.main-nav__btn.active {
  color: #4dbf9f;
  background: #fff;
}
main.nav-frame nav.main-nav ul.main-nav__list li.main-nav__item a.main-nav__btn i.icon {
  display: block;
  margin: 0 auto;
  width: 12vw;
  height: 12vw;
